body {
  /* background-color: #17181F; */
  background-color: black;
  color: #aaa;
}


*{
  box-sizing: border-box;
  font-family: sans-serif;
  font-size: 18px;
}

main {
  max-width: 400px;
  margin: 20px auto;

}
form {
  border: 3px solid #30313D;
  border-radius: 10px;

 display: flex;
 padding: 5px;

}
form input[type='text']{
  background:none;
  border: 0;
  padding: 0 8px;
  display: block;
  color: #aaa;
  width: 100%;
}

form button {
  background-color: #61DAFB;
  border: 0;
  border-radius: 6px;
  cursor: pointer;
}



 div.task {
  background-color: #30313D;
  padding: 5px 9px;
  margin-top: 5px;
  border-radius: 10px;
  display: flex;
  align-items: center;
 }

div.task svg {
  height: 20px;
  margin-right: 4px;
}

div.task-name {
  flex-grow: 1;
}

div.task span {
  position: relative;
}


div.checkbox {
  display: flex;
  align-items: center; 
  margin-right: 5px;
  cursor: pointer;
}

div.task span:before {
  content: '';
  display: block;
  width: 0;
  height: 1px;
  background-color: #ddd;
  position: absolute;
  top: 10px;
  transition: width .1s linear;
}


button.trash {
  background: none;
  border: none;
  cursor: pointer;
}

button.trash svg {
  fill: #888;
  height: 14px;
  transition: .5 ease-in;
}
button.trash svg:hover {
  fill: rgb(202, 26, 26);
  transition: .5s ease-out;
}


